import React, { Component } from 'react';
import './UserHeader.css';

class UserHeader extends Component {

    render() {
        return (
            <div className="userHeader">
                <div className="userHeader__back" onClick={this.handleClickBack}></div>
                <div className="userHeader__list">
                    <span className="userHeader__item userHeader__item--selected"></span>
                    <span className="userHeader__item"></span>
                </div>
                <div className="userHeader__right" onClick={this.handleClickLogout}></div>
            </div>
        )
    }

    handleClickBack = () => {
        this.props.onBack()
    }
    handleClickLogout = () => {
        this.props.onLogout()
    }
}

export default UserHeader;
